.homepagemain {
  /* background-color: yellow; */
  /* height: 100vh; */
  box-sizing: border-box;
  display: grid;
  grid-gap: 10px;
  grid-template-rows: 300px 200px 200px;
  grid-template-columns: 200px 500px;
  margin-top: 7%;
  z-index: 999;
}

.greetings {
  font-family: "Azeret Mono", monospace;
  /* background-color: purple; */
  text-align: center;
  color: #eb6440;
  font-size: 80px;
  /* height: 100vh; */
  /* width: 500vw; */
  /* padding-top: 200px; */
  padding-top: 100px;
  padding-left: 1px;
  padding-right: 100px;
  padding-bottom: 40px;
  /* margin-bottom: 20px; */
  /* background-color: #F5F5FA; */
  /* margin-right: 100px; */
  grid-row: 1;
  grid-column: 1 / span 2;
  overflow: hidden;
}

.spacer {
  grid-row: 2/3;
  grid-column: 1 / span 2;
  font-size: 45px;
  margin-left: 90px;
  padding-top: 40px;
  color:#2F2B2B;
  /* background-color: blueviolet; */
}

.picdiv {
  /* background-color: aqua;     */
  grid-row: 1/3;
  grid-column: 3;
  /* padding-left: 20%; */
  padding-bottom: 50px;
}

.quote {
  /* height:100vh;
    width: 100vw; */
  /* background-color: darkkhaki; */
  /* background-color: #F5F5FA; */
  color: #2F2B2B;
  grid-row: 3;
  grid-column: 1 / span 3;
  padding-top: 100px;
  padding-left: 30px;
  padding-bottom: none;
  /* font-family: 'Azeret Mono', sans-serif; */
}

.img1 {
  height: 600px;
  width: 600px;
  padding-bottom: 20px;
  margin-left: 30px;

  -webkit-filter: drop-shadow(4px 4px 0 #D6E4E5) drop-shadow(-4px 4px 0 #D6E4E5)
    drop-shadow(2px -2px 0 #D6E4E5) drop-shadow(-2px -2px 0 #D6E4E5);

  filter: drop-shadow(4px 4px 0 #D6E4E5) drop-shadow(-4px 4px 0 #D6E4E5)
    drop-shadow(4px -4px 0 #D6E4E5) drop-shadow(-4px -4px 0 #D6E4E5);
}

/* media queries so the page becomes responsive*/

@media screen and (max-width: 992px) {
  .homepagemain {
    display: flex;
    flex-direction: column;
    text-align: left;
    padding-top: none;
    padding-left: none;
    padding-right: none;
  }
  .greetings {
    overflow: hidden;
    z-index: 1;
    height: 300px;
  }
  .spacer {
    height: 69px;
  }
}

/* On screens that are 600px wide or less, the background color is olive */
@media screen and (max-width: 600px) {
  .homepagemain {
    display: flex;
    flex-direction: column;
  }

  .greetings {
    /* overflow: hidden; */
    z-index: 1;
    height: 200px;
    padding-top: 80px;
    font-size: 64px;
    padding-bottom: none;
    background-color: aqua;
  }

  .img1 {
    margin-top: 90px;
    /* margin-right: 90px; */
    margin-left: none;
    width: 300px;
    height: 300px;
  }
  .spacer {
    margin-top: none;
    padding-top: none;
    background-color: brown;
    margin-left: none;
    padding-left: none;
  }

}

